*,
*:before,
*:after {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

nav.navbar { 
  display: flex;
  padding: 10px;
  background: #0d1196; 
  justify-content: space-between;
}

.navbar-logged {
  display: inline;
  justify-content: flex-end;
}
button.navbar-button {
  padding: 5px 20px;
  width: 100px;
  border-radius: 10px;
  margin: 10px;
}
button.navbar-button a {
  text-decoration: none;
}
.home-btn {
  display: flex;
  margin-left: 15px;
  width: 50px;
  height: 50px;
}
.logo{
  width: 50px;
  height: 50px;
}

/*------ HOME PAGE -----*/
.home{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin: 25px;
}

.animeList{
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  margin-top: 20px;
}

/*------ LOGIN / SIGNUP ----- */
.login{
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 15px;
  margin: 20px;
  padding: 10px;
  border: 2px solid #0d1196;
}

/*----- DETAILS PAGE ----*/
.details{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 20px;
  gap: 20px;
}


/*---- PROFILE ------ */
.profile{
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 20px;
  gap: 10px;
}

.foto{
  width: 30%;
}
.myList{
  text-align: center;
  padding: 10px;
}
.myAnimes{
  border: 2px solid #0d1196;
  padding: 10px;
}

.myAnimu{
  display: flex;
  margin: 10px auto;
}
.myAnimu img{
  width: 30%;
  margin-right: 10px;
}

/*----- EDIT USER -------*/
.edit{
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 15px;
}
.edit-info{
  display: flex;
  flex-direction: column;
  border: 2px solid #0d1196;
  margin: 20px;
  padding: 15px;
  
}

/*------ RESPONSIVE -------*/
@media (min-width: 750px){
  .profile{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 20px;
    gap: 10px;
  }
  
  .foto{
    width: 20%;
  }
  .myList{
    text-align: center;
    padding: 20px;
  }
  .myAnimes{
    border: 2px solid #0d1196;
    padding: 20px;

  }
  .myAnimu{
    display: flex;
    margin: 10px auto;
  }
  .myAnimu img{
    width: 30%;
    margin-right: 10px;
  }
}

@media (min-width: 950px){
  .profile{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 20px;
    gap: 10px;
  }
  
  .foto{
    width: 10%;
  }
  .myList{
    text-align: center;
    padding: 30px;
  }
  .myAnimes{
    border: 2px solid #0d1196;
    padding: 30px;

  }
  .myAnimu{
    display: flex;
    margin: 10px auto;
  }
  .myAnimu img{
    width: 30%;
    margin-right: 10px;
  }
}